'use client';
import { createPost, fetchOEmbed } from '@/features/videos/video.server';
import { YoutubeOEmbedResponse } from '@/features/videos/video.type';
import { useEffect, useState } from 'react';
import Image from 'next/image';

export default function PostsPage() {
  const [url, setUrl] = useState('');
  const [text, setText] = useState('');
  const [video, setVideo] = useState<YoutubeOEmbedResponse | null>(null);
  const [urlMessage, setUrlMessage] = useState<string | null>(null);
  const [message, setMessage] = useState<string | null>(null);

  // URL入力が変わる毎にフェッチ（500msでデバウンス）
  useEffect(() => {
    if (!url) return;

    const timer = setTimeout(async () => {
      try {
        // 動画のプレビューを取得
        const video = await fetchOEmbed(url);
        setVideo(video);
        setUrlMessage(null);
      } catch (e) {
        if (e instanceof Error) {
          setUrlMessage(e.message);
          setVideo(null);
        } else {
          setUrlMessage('エラーが発生しました');
        }
      }
    }, 500);

    // クリーンアップ
    return () => {
      clearTimeout(timer);
    };
  }, [url]);

  const handleSubmit = async (formData: FormData) => {
    try {
      await createPost(formData);
      setMessage('投稿しました！'); //後でリダイレクトに変更
    } catch (e) {
      if (e instanceof Error) {
        setMessage(e.message);
      } else {
        setMessage('エラーが発生しました');
      }
    }
  };

  return (
    <div className="max-w-7xl mx-auto shadow-sm rounded-xl p-10">
      <form action={handleSubmit}>
        {/* URL */}
        <div>
          <label className="block text-sm font-medium mb-2">YouTube URL</label>
          <input
            name="url"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            placeholder="https://www.youtube.com/watch?v=..."
            className="w-full rounded-lg border border-gray-500 shadow-sm p-1.5 text-sm"
          />
          <div className="mt-1 mb-2 ml-2 text-sm">
            {urlMessage ? (
              <p className="text-red-500">{urlMessage}</p>
            ) : (
              <p className="invisible">placeholder</p>
            )}
          </div>
        </div>

        {/* プレビュー */}
        <div className="rounded-xl border border-gray-500 shadow-sm bg-gray-50 p-4 min-h-40 mb-8">
          {video ? (
            <div className="flex gap-6">
              <div className="relative w-60 aspect-video shrink-0">
                <Image
                  src={video.thumbnail_url}
                  alt={video.title}
                  fill
                  className="object-cover rounded-lg"
                />
              </div>
              <div className="flex flex-col justify-center">
                <p className="font-medium">{video.title}</p>
                <p className="text-sm mt-4">{video.author_name}</p>
              </div>
            </div>
          ) : (
            <p className="text-sm text-gray-400">URL入力後、動画のプレビューを表示します</p>
          )}
        </div>

        {/* テキスト */}
        <div>
          <label className="block text-sm font-medium mb-2">学習メモ</label>
          <textarea
            name="text"
            value={text}
            onChange={(e) => setText(e.target.value)}
            placeholder="動画を見て学んだことや記録しておきたいことを自由に書いてください"
            className="w-full rounded-lg border border-gray-500 shadow-sm p-3 text-xl min-h-120"
          />
        </div>

        {/* ボタン */}
        <button
          type="submit"
          className="rounded-lg bg-black text-white px-4 py-2 mt-4 hover:shadow-md transition"
        >
          投稿する
        </button>

        <p
          className={`mt-4 text-xl ${
            message === '投稿しました！' ? 'text-green-500' : 'text-red-500'
          }`}
        >
          {message}
        </p>
      </form>
    </div>
  );
}
